import { Component, OnInit, ViewChild, TemplateRef} from '@angular/core';
import { HostListener } from "@angular/core";
import { GalleryComponent, GalleryItem, Gallery, ImageItem, GalleryRef } from '@ngx-gallery/core';
import { Lightbox } from '@ngx-gallery/lightbox';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { map } from 'rxjs/operators';
import { ALBUMS } from '../photography/albums';
import { Photos } from '../photography/photos'
import { NavbarService } from '../navbar.service';
import { interval, Subscription} from 'rxjs';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
})
export class HomeComponent implements OnInit {
  currI = 0;
  screenHeight: number;
  screenWidth: number;
  hphotos = ALBUMS[0].imgs;

  constructor(
    public nav: NavbarService,
  ) {
  }

  ngOnInit(): void {
    this.getScreenSize();
    this.nav.show();
    this.nav.setDeg(0);

    interval(10000).subscribe(
      (val) => {
        if(this.currI < this.hphotos.length-1){
          this.currI++;
        }
        else{
          this.currI=0;
        };
    });
  }

  @HostListener('window:resize', ['$event'])

  getScreenSize(event?) {
    this.screenHeight = window.innerHeight;
    this.screenWidth = window.innerWidth;
  }

  clickImage(int:number){
    this.currI=int;
  }


}
